@import "~bootstrap/scss/bootstrap";

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap');


@import "./sub";

body
{
  position: relative;
  height: 100vh;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;

  font-family: 'Montserrat', sans-serif;
  font-family: 'Noto Sans KR', sans-serif;
}
a 
{
  text-decoration: none;
}
/* #tooltip {
  background-color: $dark_gray;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 13px;
} */

header
{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  padding: 20px 80px;

  

  .navbar 
  {
    .navbar-brand
    {
      width: 200px;
      height: 100%;
      background: url("../images/logo.svg") left center no-repeat;
      background-size: contain;
      text-indent:-10000px
    }

    .nav-link
    {
      font-size: 1.125rem;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      &::after 
      {
        content: '';        
        width: 0px;
        height: 1px;
        background-color: $white;
        margin-top: 2px;
        transition: width 0.2s;
        opacity: 0.8;
        
      }

      &:hover::after, &:focus::after
      {
        width: 100%;
      }
    }

    .navbar-toggler
    {
      border-width: 0px;
      border-radius: 0;
      padding: 0;
      box-shadow: none;
      opacity: 0.8;
      

      .navbar-toggler-icon
      {
        width: 2.75rem;
        height: 2.75rem;        
        background-image: url("data:image/svg+xml,%3Csvg width='63' height='63' viewBox='0 0 63 63' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M55.125 47.25H7.875V42H55.125V47.25ZM55.125 34.125H7.875V28.875H55.125V34.125ZM55.125 21H7.875V15.75H55.125V21Z' fill='white'/%3E%3C/svg%3E%0A");
      }
      
    }
    
    
    .icon_insta
    {
      position: relative;
      margin-left: 35px;  
      width: 40px;   
      height: 40px;
      border: none;
      border-radius: 50%;
      background: url('../images/icon_insta.svg') center center;
      background-color: rgba( $accent-orange, 0.0);
      background-repeat: no-repeat;
      background-size: 20px;   
      z-index: 0;   
      transition: background 0.3s;

      &::before
      {
        content: '';
        position: absolute;
        left: -25px;
        top: 50%;
        transform: translateY(-50%);
        width: 1px;
        height: 20px;
        background: rgba($white, 0.1);
      }  

      

      &:hover
        {
          background-color: rgba($dark-blue, 0.7);
          
        }
    }

        
  }

  &.sub 
  {
    background-color: rgba($white, 0.8);
    border-bottom: 1px solid rgba($black, 0.1);
    /* -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px); */

    .navbar
    {
      .navbar-brand 
      {

        background-image: url("../images/logo_d.svg");
      }

      .nav-link 
      {
        color: $black;

        &::after
        {
          background-color: $dark-bluegray;
        }
      }
      .navbar-toggler-icon
      {
        background-image: url("data:image/svg+xml,%3Csvg width='63' height='63' viewBox='0 0 63 63' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M55.125 47.25H7.875V42H55.125V47.25ZM55.125 34.125H7.875V28.875H55.125V34.125ZM55.125 21H7.875V15.75H55.125V21Z' fill='black'/%3E%3C/svg%3E%0A")
      }


      .icon_insta
      {
        background-image: url('../images/icon_insta_d.svg');

        &::before
        {
          background: rgba($black, 0.2);
        }

        &:hover, &:focus
        {
          background-image: url('../images/icon_insta.svg');
        }
      }

      @include media-breakpoint-down(lg) {
        .nav-link 
        {
          color: white;          
        }

        .icon_insta
        {
          background-image: url('../images/icon_insta.svg');

          &::before
          {
            background: rgba($white, 0.1);
          }
        }
      }
      
    }
    
  }
  
  @include media-breakpoint-down(lg) {
    

    .navbar 
    {         

      .navbar-brand 
      {
        width: 160px;
      }
      
      .nav-link
      {
        display: block;
        &:hover
        {
          color: $accent-orange;
        }
        &::after
        {
          display: none;
        }
      }
      
        
        .icon_insta
      {
        
        justify-content: flex-start;
        margin-left: 0px; 
        padding-top: 20px;
        margin-top: 30px;
        background-color: rgba($light-blue, 0.0);
        
        
        &::before
        {
          top: -15px;
          left: 0;
          transform: none;
          width: 367px;
          height: 1px;
          margin-bottom: 20px;
        }

        &:hover
        {
          background-color: $accent-orange;
          
        }
      }
    }

  }



}

main 
{
  overflow: hidden;

  .banner_side
  {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    right: -100px;
    bottom: 0;
    top: 25%;
    transform: translateY(-50%);
    gap: 10px;
    width: 50px;
    height: 250px;
    padding: 18px 8px;    
    background-color: $accent-orange;
    z-index: 10000;
    animation-name: bn_side_ani;
    animation-delay: 1.0s;
    animation-duration: 1.0s;
    animation-fill-mode: forwards;

    

    img 
    {
      
      width: 27px;
      height: 27px;
      opacity: 0.5;
      flex-grow: 0;
      transform: rotate(90deg);
    }
    span 
    {
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: 0.06em;


      width: 10.625rem;
      flex-grow: 1;
      color: $white;
      font-family: 'Montserrat',;      
      font-style: normal;
      font-weight: 600;
      font-size: 26px;
      line-height: 100%;
      transform: rotate(90deg);

    }
  }

  .banner_bottom 
  { 
    
    position: fixed;
    left: 0;
    bottom:-150px;
    opacity: 1;
    display: flex;
    word-break: keep-all;
    -webkit-backdrop-filter: blur(5px);
              backdrop-filter: blur(5px);
    z-index: 10000;
    animation-name: bn_bottom_ani;
    animation-delay: 0.6s;
    animation-duration: 1.0s;
    animation-fill-mode: forwards;
    

    
    .bn_1
    {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 1;
      gap: 15px;
      padding: 22px 35px;
      color: $white;
      font-size: 1.375rem;
      font-weight: 400;
      background-color: rgba($accent-orange, 0.7);
      border-right: 1px solid rgba($black, 0.2);
      
      
      img 
      {opacity: 0.4;}

      span 
      {
        opacity: 0.75;
      }

      &:hover, &:focus
      {
        background: rgba($accent-orange, 0.9);
        cursor: pointer;

        img 
        {
          opacity: 0.8;
        }
        span 
        {
          opacity: 1;
        }
      }
    }

    .bn_2
    {
      flex-shrink: 2;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0px 35px;
      gap: 39px;
      background: rgba($dark-blue, 0.7);
      

      h3
      {
        font-size: 1.75rem;
        font-weight: 400;
        margin: 0;
        color: $white;
      }
      span 
      {
        max-width: 369px;
        font-size: 0.875rem;
        font-weight: 200;
        color: $white;
        line-height: 1.3;
        opacity: 0.7;
        
      }
      .bn_2_btn 
      {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 50px;
        min-height: 50px;
        opacity: 0.6;
        border: 1px solid $white;
        border-radius: 50%;
        
        background: url(../images/icon_arrow2.svg) center no-repeat;
      }
      
      &:hover, &:focus
      {
        background: rgba($dark-blue, 0.9);
      }
    }
   
    
    
    
  }

  @keyframes bn_side_ani {
    from    {right: -50px;}
    to      {right: 0px;}
  }
  @keyframes bn_bottom_ani {
    from    {bottom:-150px; opacity: 0;}
    to      {bottom:0px; opacity: 1;}
  }


  .banner_side.bn_show, .banner_bottom.bn_show
  {
    

    .bn_1
    {
      
      padding: 10px 30px;
      background: rgba($accent-orange, 1);

      img 
      {
        transform: scale(0.7);
      }
      span 
      {display:none;}
    }
    .bn_2 
    {
      padding: 0px 25px;
      background: rgba($dark-blue, 1);
      h3
      {
        font-size: 1.375rem;
      }
      span 
      {
        display: none !important;
      }
      .bn_2_btn 
      {
        transform: scale(0.7);
      }

      
    }
  }

  @include media-breakpoint-down(xl) {
    .banner_bottom
    {
      .bn_1 {
        font-size: 1.25rem;
        padding: 10px 30px;
      }
      .bn_2 
      {
        padding: 0px 25px;
        h3
        {
          font-size: 1.375rem;
        }
        .bn_2_btn 
        {
          transform: scale(0.7);
        }
      }
    }

  }

  @include media-breakpoint-down(lg) {
    
    .navbar 
    {
      .icon_insta
    {
      
      justify-content: flex-start;
      margin-left: 0px; 
      padding-top: 20px;
      margin-top: 10px;

      &::before
      {
        top: 0;
        left: 0;
        transform: none;
        width: 100%;
        height: 1px;
      }

      &:hover
      {
        &::after
        {
          left: -11px;
          top: 8px;
          transform: none;
          background: rgba($light-blue, 0.7);
        }
      }
    }
    }
    

    .banner_side, .banner_bottom
    {
      display: none;
    }
  }

}

section
{
  display: flex;  
  transition: 0.5s all ;
}

/* Main Visual */
.hero
{
  height: 100vh;
  
  .swiper 
  {
    width: 100%;
    height: 100%;

    .swiper-button-next, .swiper-button-prev 
    {
      color: white;
      opacity: 0.3;
    }
    .swiper-button-next:hover, .swiper-button-prev:hover 
    {
      opacity: 0.7;
    }

    @include media-breakpoint-down(lg) {
      .swiper-button-next, .swiper-button-prev 
      {
        display: none;
      }
    }


    .swiper-slide_box 
      {
        position: relative;
        display: flex;
        width: 100%;
        height: 100%;

        .img_box
        {
          display: flex;
          width: 100%;
          height: 100%;
          background-repeat: no-repeat;
          background-size: cover;
          background-position-x: center;
        }

        .img_item_upper
        {
          position: absolute;
          right: 0;
          bottom: 0;
          min-width: 100%; 
          min-height: 100%;    
          
          background: -webkit-gradient(linear, left top, left bottom, from(rgba(27, 46, 70, 0.85)), color-stop(30.31%, rgba(27, 46, 70, 0))), 
                      -webkit-gradient(linear, left top, right top, color-stop(21.25%, rgba(44, 56, 72, 0.8)), color-stop(66.15%, rgba(44, 56, 72, 0.16))),
                      rgba(31, 86, 169, 0.2), 
                      url(/src/assets/images/dot_2_3.png);
          background: -o-linear-gradient(top, rgba(27, 46, 70, 0.85) 0%, rgba(27, 46, 70, 0) 30.31%), 
                      -o-linear-gradient(left, rgba(44, 56, 72, 0.8) 21.25%, rgba(44, 56, 72, 0.16) 66.15%), 
                      rgba(31, 86, 169, 0.2), 
                      url(/src/assets/images/dot_2_3.png);
          background: linear-gradient(180deg, rgba(27, 46, 70, 0.85) 0%, rgba(27, 46, 70, 0) 30.31%), 
                      linear-gradient(90deg, rgba(44, 56, 72, 0.8) 21.25%, rgba(44, 56, 72, 0.16) 66.15%), 
                      rgba(31, 86, 169, 0.2), 
                      url(/src/assets/images/dot_2_3.png);
          mix-blend-mode: multiply;
        }

        .img_item
        {          
          min-width: 100%; 
          min-height: 100%;
          max-height: 100%;
          object-fit: cover;
        }

        .title_box
        {

          position: absolute;
          left: 0;
          top: 50%;
          margin-left: 0;
          margin-right: 0;
          padding: 5% 5% 5% 12%;
          transform: translateY(-50%);
                    

          h1
          {
            font-size: 3.4375rem;
            font-weight: 500;
            color : $white;
            text-transform: uppercase;
          }
          p
          {
            font-weight: 300;
            font-size: 1.25rem;
            color : $white;
            margin: 20px 0 30px;
            opacity: 0.7;
          }

          .btn 
          {
            position: relative;
            border-radius: 50px;
            border-color: rgba($white, 0.7);
            padding: 10px 20px;
            padding-right: 70px;
            font-weight: 400;
            font-size: 1.125rem;
            color: $white;
            overflow: hidden;
            transition: all 0.3s ease-in-out;

            &::before 
            {
              content: '';
              position: absolute;
              right: 50px;
              top: 50%;
              width: 40px;
              height: 8px;
              transform: translateY(-50%);          
              background-image: url("data:image/svg+xml,%3Csvg width='33' height='8' viewBox='0 0 33 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M32.3545 4.35355C32.5498 4.15829 32.5498 3.84171 32.3545 3.64645L29.1725 0.464466C28.9773 0.269204 28.6607 0.269204 28.4654 0.464466C28.2702 0.659728 28.2702 0.976311 28.4654 1.17157L31.2939 4L28.4654 6.82843C28.2702 7.02369 28.2702 7.34027 28.4654 7.53553C28.6607 7.7308 28.9773 7.7308 29.1725 7.53553L32.3545 4.35355ZM0.000976562 4.5H32.001V3.5H0.000976562V4.5Z' fill='black'/%3E%3C/svg%3E");
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
              overflow: hidden;
              opacity: 0;

            }
            &::after 
            {
              content: '';
              position: absolute;
              right: 10px;
              top: 50%;
              width: 40px;
              height: 8px;
              transform: translateY(-50%);          
              background-image: url("data:image/svg+xml,%3Csvg width='33' height='8' viewBox='0 0 33 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M32.3545 4.35355C32.5498 4.15829 32.5498 3.84171 32.3545 3.64645L29.1725 0.464466C28.9773 0.269204 28.6607 0.269204 28.4654 0.464466C28.2702 0.659728 28.2702 0.976311 28.4654 1.17157L31.2939 4L28.4654 6.82843C28.2702 7.02369 28.2702 7.34027 28.4654 7.53553C28.6607 7.7308 28.9773 7.7308 29.1725 7.53553L32.3545 4.35355ZM0.000976562 4.5H32.001V3.5H0.000976562V4.5Z' fill='white'/%3E%3C/svg%3E");
              background-repeat: no-repeat;
              transition: all 0.3s ease-in-out;
            }

            &:hover 
            {

              background-color: $accent-orange;
              border-color: $accent-orange;
              color: $black;

              &::before
              {
                right: 10px;
                opacity: 1;
              }

              &::after 
              {           
                right: -60px;            
              }
            }
          }
          

          
          @include media-breakpoint-down(lg) {

            width: 100vw;
            padding: 10% 10 10% 12%;

            h1
            {
              font-size: 2.875rem;
            }
            p 
            {
              font-size: 1.125rem;
            }
          }

          @include media-breakpoint-down(md) {

            padding: 10% 10 10% 12%;
            h1
            {
              font-size: 1.875rem;
            }
            p 
            {
              font-size: 1.0rem;
            }
            .btn  
            {
              font-size: 1.0rem;
            }

            
          }



        }
      }
    
    }

  

}

