/*    */
.section_02
{
  margin-top: 140px;
  z-index: 0;

  .sec_card_wrap
  {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    transition: 0.5s all ;  
    
    &:nth-child(0), &:nth-child(1)
    {
      margin-bottom: 15%;
    }
    
    .sec_card
    {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;     
      
      .sec_img
      {
        width: 100%;
        max-width: 400px;
        transition: 0.5s all ;

        img 
        {     
          
          width: 100%;
          height: 100%;
        }
      }
      

      .sec_card_txt
      {
        min-width: 200px;
        display: flex;
        flex-grow: 1;
        flex-direction: column;
        padding:1.875rem ;
        margin-right: 1.875rem;

        span
        {
          font-weight: 500;
          font-size: 1.875rem;
          letter-spacing: -0.015em;
          color: $accent-orange;
          margin-bottom: 30px;
          position: relative;
          display: inline-block;
          width: fit-content;

          &::after
          {
            content:'';
            width:30px;
            height: 21px;
            position: absolute;
            right: -3.125rem;
            top: 50%;  
            transform: translateY(-50%);      
            background: url(../images/icon_check_l.svg);
            
          }
        }
        p 
        {
          font-weight: 400;
          font-size: 1.25rem;
          color: $dark-bluegray;
          margin: 0;
        }
      }     
      
    }    

    @include media-breakpoint-down(xxl) {
      &:nth-child(0), &:nth-child(1)
      {
        margin-bottom: 0%;
      }
    }

    @include media-breakpoint-down(lg) {
      
    
  
      & .sec_card
      {
        width: 100%;

        .sec_img {
          max-width: 100% ;
        }

        .sec_card_txt
        {
          flex-direction: row;
          min-width: 100%;
          width: 100%;
          margin-right: 0;
          padding-top: 5%;

          span
          {
            margin-right: 20%;
            margin-left: 10%;
            font-size: 1.5rem;

            &::after
              {             
                
                right: -2.1875rem;   
                width: 20px;  
                height: 15px;
                background-size: contain;  
                background-repeat: no-repeat;       
              }
          }
          p 
          {
            font-size: 1.125rem;
          }
        }
      }
      
    }
    @include media-breakpoint-down(md) {
      
        & .sec_card
        {


            .sec_card_txt
          {
            padding:1.875rem 0;

            span 
            {
              margin-left: 5%;
              margin-right: 20%;
              font-size: 1.375rem;
                
            }
            
          }
        }       
        
    }

      

      
  }

  
  
}




/*    */
.section_03
{
  margin-top: 0px;

  

  .sec_card
  {
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
    transition: all 0.3s ease-in;
    

    .sec_img
    {
      position: relative;
      overflow: hidden;
      z-index: 0;
      height: 100%;
      transition: all 0.3s ease-in;
      
      img 
      {
        width: 100%;
        height: 100%;
        transition: all 0.3s ease-in;
      }

      &::before
      {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background: rgba($light-blue, 0.5), rgba(38, 41, 49, 0.4);
        background-blend-mode: soft-light, multiply;
        backdrop-filter: blur(0px);
        z-index: 1;
      }
    }


    .sec_card_txt
    {
      position: absolute;
      left: 14%;
      bottom: 10%;
      h3
      {
        font-weight: 500;
        font-size: 1.875rem;
        letter-spacing: -0.015em;
        color: $white;
        margin-bottom: 1.375rem;
        position: relative;
      }

      p 
        {
          font-weight: 300;
          font-size: 1.125rem;
          color: $white;
        }
    }

    &:hover
    {
      -webkit-box-shadow: 0px 10px 30px rgba($light-blue, 0.5);
              box-shadow: 0px 10px 30px rgba($light-blue, 0.5);

      .sec_img 
      {
        img
        {
          scale: 1.1;          
        }
        
        &::before
      {
        backdrop-filter: blur(5px);
      }
      }
      
    }
  }

  

@include media-breakpoint-down(lg) {
  margin-top: 140px;
}

 
}


/*    */
.section_04
{
  margin-top: 140px;
  padding-bottom: 100px;

  word-break: keep-all;
  position: relative;

  &::after 
  {
    content: '';
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
    z-index: -1;
    opacity: 0.5;

    background: -webkit-gradient(linear, left top, left bottom, from($white), color-stop(81.07%, rgba($white, 0))), url(/src/assets/images/dot_2_3.png), rgba($dark-blue, 0.05);
    background: -o-linear-gradient(top, $white 0%, rgba($white, 0) 81.07%), url(/src/assets/images/dot_2_3.png), rgba($dark-blue, 0.05);
    background: linear-gradient(180deg, $white 0%, rgba($white, 0) 81.07%), url(/src/assets/images/dot_2_3.png), rgba($dark-blue, 0.05);
  
  }
  
  .sec4_line > div
  {
    position: relative;
  }
  .sec4_line > div::after
  {    
    content: '';
    width: 1px;
    height: 74px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background:$black;
    opacity: 0.15;
  }
  .sec4_line > div:last-child::after
  {
    opacity: 0;
  }

  .icon_circle
  {
    border-radius: 50%;  
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $black;
    width: 120px;
    height: 120px;
    background-color: transparent;
    transition: 0.5s background-color;
    
    &:hover, &:focus
    {
      background-color: $accent-orange;

      img {        
        filter: brightness(0) invert(1);        
      }
    }
  }

  img 
  {
    width: 53px;
    margin: 0 auto;
  }
  p 
  {
    margin-top: 40px;
    font-size: 0.8125rem;
    text-align: center;
    text-transform: uppercase;
    opacity: 0.6;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: -0.01em;
    align-self: center;
  }
  h3 
  {
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: -0.015em;
    text-align: center;
    margin-top: 20px;
  }


  @include media-breakpoint-down(lg) {
    .row > div 
    {
      margin-bottom: 10% ;
    }
    .sec4_line > div:nth-child(even)::after
    {
      opacity: 0;
    }
    h3 {
      font-size: 1.25rem;
    } 
  }
  @include media-breakpoint-down(md) {

    
    .sec4_line > div::after
    {
      opacity: 0;
    }
  }
}

/*   /   */
.section_05
{
  margin-top: 140px;
  padding-bottom: 140px;

  .add_margin > div:nth-child(even)
  {
    margin-top: 70px;
  }
  .sec_card
  {    
    display: block;
    position: relative;
    cursor: pointer;    
    overflow: hidden;

    
    
    img 
    {      
      max-width: 210px;
      min-width: 100%; 
      transition: all 0.3s ease-in; 
    }   

    span 
    {
      position: absolute;
      display: inline-block;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-weight: 500;
      font-size: 22px;
      color: $dark-bluegray;      
      padding: 20px 30px;
      transition: all 0.3 ease-in;
      word-break: keep-all;
      
      &::before
      {        
        content: '';
        position: absolute;
        width: 120%;
        height: 120%;
        top: 50%;
        left: 50%;
        z-index: -1;
        transform: translate(-50%, -50%);
        background: rgba($white, 0.5);
        backdrop-filter: blur(3px);
        border-radius: 100px;
        -webkit-box-shadow: 0px 3px 28px 0px rgba(24, 40, 75, 0.3); 
                box-shadow: 0px 3px 28px 0px rgba(24, 40, 75, 0.3);
      }

      &::after
      {        
        content: '';
        position: absolute;
        z-index: -2;
        width: 0%;
        height: 0%;
        top: 50%;
        left: 50%;
        opacity: 0;
        /* backdrop-filter: blur(3px); */
        transform: translate(-50%, -50%);
        transition: all 0.2s ease-in;
      }
    }

    

    &:hover, &:focus
    {    
      &::before
      {
        background: rgba($dark-blue, 0.15);  
      }
      
      
      img
      {
        scale: 1.1;
      }
      
      span
      {
        
        color: $white;
        transition: all 0.3 ease-in;

        &::before
        {          
          border: 1px solid $white;
          background: none;
          backdrop-filter: none;
        }

        &::after
        {
          width: 500%;
          height: 700%;
          background: rgba(53, 74, 117, 0.7); 
          opacity: 1;

          @include media-breakpoint-down(lg) {
            width: 1500%;
            height: 2000%;
          }
        }
      }        
      
    }    
    
 }


@include media-breakpoint-down(lg) {
  .add_margin > div:nth-child(even)
  {
    margin-top: 0px;
  }
}


}

/*   ! */
.section_06
{
  background: -webkit-gradient(linear, left bottom, left top, from(rgba($dark-blue, 0.5)), to(rgba($dark-blue, 0.5))), -webkit-gradient(linear, left bottom, left top, from(rgba($dark-blue, 0.2)), to(rgba($dark-blue, 0.2))), url(/src/assets/images/img_5.png) center;
  background: -o-linear-gradient(bottom, rgba($dark-blue, 0.5), rgba($dark-blue, 0.5)), -o-linear-gradient(bottom, rgba($dark-blue, 0.2), rgba($dark-blue, 0.2)), url(/src/assets/images/img_5.png) center;
  background: linear-gradient(0deg, rgba($dark-blue, 0.5), rgba($dark-blue, 0.5)), linear-gradient(0deg, rgba($dark-blue, 0.2), rgba($dark-blue, 0.2)), url(/src/assets/images/img_5.png) center;
  background-size: cover;
  position: relative;
  z-index: -1;
  color: $white;

  &::before
  {
    content: '';
