    font-size: 2.5rem;
    padding: 0 50px;
    word-break: keep-all;
    line-height: 1.5;
  }
  span 
  {
    width: 67px;
    height: 1px;
    background: $white;
    opacity: 0.3;
    margin: 45px 0px;
  }

  p 
  {
    font-weight: 300;
    font-size: 18px;
    opacity: .8;
    padding: 0 50px;
    word-break: keep-all;
  }

  @include media-breakpoint-down(lg) {
    span 
    {
      margin: 20px 0px;
    }
    h2, h3 
    {
