    {
      font-size: 1.25rem;
    }
    
  }


}

/* FOOTER */
.footer
{
  background: rgba($dark-bluegray, 0.08);

  .container
  {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }

  .info 
  {
    list-style: none;
    
    li 
    {
      color: rgba($black, 0.7);
      font-weight: 400;
      font-size: 14px;
    }
    
  }

  .menu 
  {
    list-style: none;

    li a 
    {
      text-decoration: none;
      color: rgba($black, 1.0);
      font-weight: 400;
      font-size: 14px;

      &:hover, &:focus
      {
        color: rgb(42, 67, 121);
        position: relative;

        &::before
        {
          content: '';
          width: 100%;
          height: 1px;
          left: 0;
          bottom: -2px;
          position: absolute;
          background:  rgb(42, 67, 121);
        }
      }
    }
  }

  .call
  {
    display: flex;
    flex-direction: column;
    text-align: right;
    p 
    {
      font-weight: 400;
      font-size: 18px;
      color: $dark-bluegray;;
      margin: 0;
    }
    span 
    {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 600;
      font-size: 2.875rem;
      color: $dark-blue;
    }
    a 
    {      
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: auto;      
      padding: 10px; 
      width: 40px;    
      height: 40px;    
      border-radius: 50%;
      background: rgba(5, 33, 66, 0.3);
      transition: background 0.3s ;

      img 
      {
        width: 20px;
        height: 20px;
      }

      &:hover, &:focus
      {
        background: rgba(5, 33, 66, 0.7);
      }
    }
    
  }

  @include media-breakpoint-down(lg) {
    .container
    {
      padding-top: 1.5rem;
      padding-bottom: 1.5rem;
    }
    .row
    {
      display: flex;
      justify-content: space-between;
    }
    .row > *
    {
      width: auto;
    }
    
    
    .logo, .menu 
    {
      display: none;
    }
    .call
    {
      p
      {
        font-size: 1rem;
      }
      span 
      {
        font-size: 1.875rem;
      }
    }
    
  }

  @include media-breakpoint-down(md) {
    .container
    {
      
      .info
      {
        font-size: 12px;
      }
    }
    .call
    {
      span 
      {
        font-size: 1.375rem;
      }
    }
  }

}



/* ===== TOP Button ===== */
.top-link
  {    
    text-decoration: none;
    font-size: 0.75rem;
    font-weight: bold;
    position: fixed;
    bottom: 3rem;
    right: 3rem;
    background: $primary;
    width: 2rem;
    height: 2rem;
    display: grid;
    place-items: center;
    border-radius: $border-radius;
    color: $light;
    animation: bounce 2s ease-in-out infinite;
    visibility: hidden;
    z-index: -100;
  }
  .show-link {
    visibility: visible;
    z-index: 100;
  }
  
@keyframes bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}


@include media-breakpoint-down(xl) {
  header 
  {
    padding-left: 20px;
    padding-right: 20px;
    

   /*  .icon_insta
    {
      margin-left:10px;    
      
      &::before
      {        
        left: -10px;
        
      }
    }   */
  }
}


@include media-breakpoint-down(md) {
  .section_01, .section_02, .section_03, .section_04, .section_05
  {

    margin-top: 100px;
  }
  .section_04 
  {
    padding-bottom: 10px;
  }
  .section_05 
  {
    padding-bottom: 0px;
  }

  
}
